.modal {
  top: 0;
  max-width: 100%;
}

.modal-title {
  margin-left: 1rem;
  font-size: 1.25rem;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  color: #45ADA8;
}

.modal-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  border-bottom: 1px solid #e9ecef;
}

.modal-content {
  max-width: 500px;
  margin: 1.75rem auto;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.3rem;
}

.modal-main {
  position: fixed;
  background: white;
  width: 80%;
  height: auto;
  top: 50%;
  left: 50%;
}

.modal-body {
  font-size: 1.25rem;
  margin: 0 1rem;
  white-space: pre-wrap;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  color: #594F4F;
}

.modal-footer {
  display: -webkit-flex;
  display: flex;
  padding: 1rem;
  border-top: 1px solid #e9ecef;
  color: #594F4F;
}

.close {
  margin: auto 0;
  color: #594F4F;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

.row {
  max-width: 100%;
}

.max-width {
  width: 100%;
}

.red-border {
  border: 2px solid rgb(204, 15, 103) !important;
}

.red-text {
  font-weight: bold;
  color: rgb(204, 15, 103);
}

.no-border {
  border:none;
}

.close {
  text-shadow: none;
  font-size: 1.2rem;
  opacity: 1;
}

body {
  background-color: #547980;
  color: #9DE0AD;
}

.subhead {
  color:#ffffff;
}

.btn {
  margin: 0px 0px 10px 10px;
  background-color: #9DE0AD;
  border-color: #9DE0AD;
  color: #547980;
}

.btn:hover, .btn:visited, .btn:active, .btn:link, .btn:focus {
  background-color: #9DE0AD !important;
  border-color: #9DE0AD !important;
  color: #547980 !important;
}

label, input {
  margin: 0px 0px 10px 10px !important;
}

.input-range__label {
  font-weight: bold;
}

a, .input-range__label {
  color: #9DE0AD;
}

a:visited, a:hover{
  color: inherit;
}

.no-underline:hover {
  text-decoration: none;
}

a:hover {
  font-weight: bold;
  cursor: pointer;
}

a:active {
  border-color: none;
}

.letter-input {
  width: 85px !important;
}

.input-range__track--active {
  background-color: #9DE0AD;
}

.input-range__slider {
  background-color: #9DE0AD;
  border: 1px solid #9DE0AD;
}

.input-range {
  min-width: 200px !important;
  margin-left: 10px !important;
  width: 85% !important;
}

.letter-input {
  margin: 0 !important;
}

.link-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: #E5FCC2;
  display: inline;
  margin: 0px 0px 5px 0px;
  padding: 0;
}

.link-button:hover {
  color: #E5FCC2;
  font-weight: bold;
}

.number-form {
  width: 90%;
  max-width: 90%;
}

.form-inline {
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-align-items: self-start;
          align-items: self-start;
}

.info {
  background-color: #547980;
  border: none;
  cursor: pointer;
}

.space-around {
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.hover:hover {
  font-weight: bold;
}

.white-text, .text-white {
  color: #547980;
}

.fa-info-circle {
  color: #E5FCC2;
}

.dropdown-menu {
  background-color: #9DE0AD;
  color: #547980;
}

.dropdown-item {
  background-color: #9DE0AD;
  color: #547980;
}

.dropdown-item:hover {
  background-color: #E5FCC2;
  color: #547980;
}

li {
  cursor: pointer;
}

.submit {
  background-color: #E5FCC2;
  border-color: #E5FCC2;
}

.submit:hover, .submit:visited, .submit:active, .submit:link, .submit:focus {
  background-color: #E5FCC2 !important;
  border-color: #E5FCC2 !important;
}

.number {
  width: 80px !important;
}

.bm-menu-wrap{
  width: 200px !important;
}

.bm-item {
  text-decoration: none;
  margin-bottom: 10px;
  color: #547980;
}

.bm-item:active, .bm-item:visited, .bm-item:hover, .bm-item:link {
  text-decoration: none;
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 24px;
  height: 20px;
  left: 24px;
  top: 24px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #E5FCC2;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
  color: #547980 !important;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #547980 !important;
}

/* General sidebar styles */
.bm-menu {
  background: #9DE0AD;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Wrapper for item list */
.bm-item-list {
  color: #547980;
}

/* Wrapper for item list */
.bm-item:focus {
  outline:none;
}

/* Wrapper for item list */
.menu-item:focus {
  outline:none;
}

.image-size {
  width: 60%;
  height:auto;
}

.yellow-text {
  color: #E5FCC2;
}

@media only screen and (min-width:0px) and (max-width:600px){
  .form-inline {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
  .image-size {
    width: 100%;
  }
}
